@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css?family=Roboto:400,700,900&subset=cyrillic");
@import url(../css/magnific-popup.css);
* {
  padding: 0;
  margin: 0;
  border: 0; }

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

:focus, :active {
  outline: none; }

a:focus, a:active {
  outline: none; }

nav, footer, header, aside {
  display: block; }

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

input, button, textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a, a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit; }

/*--------------------*/
a {
  color: #000 !important;
  text-decoration: none; }

a.text:active,
a.text:hover,
a.text {
  text-decoration: none;
  color: #666; }

* {
  margin: 0;
  padding: 0; }

html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'Roboto', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  max-width: 100%;
  min-width: 320px; }

.content {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto; }

.wrap {
  min-width: 320px;
  max-width: 1024px;
  padding: 0 15px;
  margin: 0 auto;
  box-sizing: border-box; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.header {
  height: 100vh;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  /* Do not repeat the image */
  padding-top: 150px; }

.header__inner {
  display: block;
  margin-left: 240px;
  text-align: center;
  max-width: 506px;
  height: 506px;
  border-radius: 50%;
  background-color: #fff;
  border: 10px solid #f4d4a5; }

.header__logo {
  width: 39px;
  height: 30px;
  margin-top: 10px;
  margin-bottom: 16px; }

.header__name {
  font-family: 'Lato', sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 35px;
  color: #bf4200;
  margin-bottom: 27px; }

.header__phone {
  font-family: 'Roboto';
  font-size: 35px;
  font-weight: 300;
  text-align: center;
  color: #000000; }

.header__title {
  font-family: 'Roboto';
  font-size: 50px;
  font-weight: bold;
  text-align: center;
  color: #1fad26;
  margin: 45px 0px 45px 0px; }

.main {
  text-align: center;
  margin-top: 80px; }

.main__title {
  font-size: 40px;
  color: #d07a2d;
  margin-bottom: 40px; }

.main__about {
  font-size: 30px;
  font-weight: 300; }

.main__subtitle {
  font-weight: bold;
  margin-top: 30px;
  margin-bottom: 90px;
  font-size: 30px; }

.wooden__img {
  max-width: 160px;
  max-height: 115px;
  margin: 0 auto; }

.project {
  text-align: center;
  margin: 0 auto;
  padding-top: 100px; }

.project__title {
  font-family: 'Roboto';
  font-size: 41px;
  font-weight: bold;
  margin-bottom: 115px;
  color: #bf4200; }

.project__item {
  margin-bottom: 30px; }

.project__name {
  font-size: 25px;
  font-weight: bold; }

.project__price {
  color: #17753b;
  margin-bottom: 30px; }

.download {
  text-align: center; }

.download__wrap {
  background-color: #F1DFBC;
  display: block;
  padding-bottom: 40px; }

.download__img {
  margin-top: 40px; }

.download__btn {
  display: block;
  max-width: 594px;
  border: solid 5px #effd15;
  background-color: #17753b;
  margin: 0 auto; }
  .download__btn .button__txt {
    padding: 30px 40px 30px 40px;
    color: #FFFFFF;
    font-family: 'Roboto', sans-serif;
    font-size: 25px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase; }

.project__gallery {
  margin-top: 120px;
  text-align: center;
  display: flex;
  flex-wrap: wrap; }

.gallery__title {
  color: #D07A2D;
  font-family: 'Roboto';
  font-weight: bold;
  font-size: 41px;
  text-align: center;
  margin-top: 120px; }

.gallery__text {
  font-family: 'Roboto';
  font-size: 25px;
  text-align: center;
  margin-top: 25px; }

.footer {
  flex-shrink: 0;
  color: #000;
  padding-top: 120px; }

.footer__menu {
  padding-top: 150px;
  padding-bottom: 190px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 50px; }
  .footer__menu li {
    margin-top: 50px; }

.menu__phone {
  color: #000000;
  font-family: 'Roboto';
  font-weight: bold;
  font-size: 41px;
  text-align: center; }

.menu__mail {
  color: blue;
  font-size: 30px; }

